* {
  box-sizing: border-box;
}

body {
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', Roboto, Oxygen, Ubuntu, Cantarell,
    'Open Sans', 'Helvetica Neue', sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  background-color: #fafafa;
  color: #212121;
  line-height: 1.5;
  padding: 30px;
}

#search-box {
  font: inherit;
  padding: 8px;
  max-width: 320px;
  margin-bottom: 16px;
}

ul li {
  list-style: none;
}

.country-list {
  padding: 0;
}

.list-item {
  display: flex;
  height: 30px;
  align-items: center;
  padding: 0;
}

.list-item:not(:last-child) {
  margin-bottom: 10px;
}

.list-item__icon {
  width: 30px;
  height: auto;
  margin-right: 5px;
}

.list-item__title {
  font-size: 20px;
}
.country-list {
  margin: 0;
}

.card-wraper {
  display: flex;
  align-items: center;
}

.card-img {
  width: 80px;
  height: auto;
  margin-right: 20px;
}

.card-title {
  margin: 0;
  font-size: 60px;
}

.card-list {
  padding: 0;
  margin: 0;
  font-size: 26px;
}

.card-item {
  display: flex;
  align-items: center;
}

.card-item__title {
  margin: 0 10px 0 0;
}

.card-item__text {
  margin: 0;
}
